.comment {
    background-color: #f5f5f5;
    padding: 16px;
    margin: 8px 0;
    border-radius: 8px;
}

.comment__author {
    font-weight: bold;
    margin-bottom: 8px;
}

.comment__date {
    padding-bottom: 8px;
    margin-bottom: 8px;
}

.comment__content {
    white-space: pre-line;
}
